import React from "react";
import { SELECT_ARRAYS } from "core/constants";
import { NumberInput, Select, SelectWithIcons, ValueInput } from "components";
import cn from "classnames";
import s from "./NewRuleTable.module.css";

type Props = {
  categoryIndex: number;
  fieldIndex: number;
  operationIndex: number;
  caseSensitivityIndex: number;
  value1: string;
  value2: string;
  lessMoreIndex: number;
  lessMoreAmount: string;
  onCategoryChange: (event: any) => void;
  onFieldChange: (event: any) => void;
  onOperationChange: (event: any) => void;
  onCaseSensitivityChange: (event: any) => void;
  onValueChange: (event: any, valueIndex: number) => void;
  onLessMoreChange: (event: any) => void;
  onLessMoreAmountChange: (value: string) => void;
};

// ----------
// COMPONENT:
const NewRuleTable = ({
  categoryIndex,
  fieldIndex,
  operationIndex,
  caseSensitivityIndex,
  value1,
  value2,
  lessMoreIndex,
  lessMoreAmount,
  onCategoryChange,
  onFieldChange,
  onOperationChange,
  onCaseSensitivityChange,
  onValueChange,
  onLessMoreChange,
  onLessMoreAmountChange,
}: Props) => {
  const { CATEGORIES, FIELDS, NO_YES, OPERATIONS, LESS_MORE } = SELECT_ARRAYS;

  return (
    <div className={s.root}>
      <div className={cn("flexRowWithMargin", s.equalWidth, s.headerRow)}>
        <div>Category</div>
        <div>Field</div>
        <div>Operation</div>
        <div>Case sensitive</div>
      </div>

      <div className={cn("flexRowWithMargin", s.equalWidth)}>
        <SelectWithIcons
          items={CATEGORIES}
          selected={categoryIndex}
          onChange={onCategoryChange}
        />
        <Select items={FIELDS} selected={fieldIndex} onChange={onFieldChange} />
        <Select
          items={OPERATIONS}
          selected={operationIndex}
          onChange={onOperationChange}
        />
        <Select
          items={NO_YES}
          selected={caseSensitivityIndex}
          onChange={onCaseSensitivityChange}
        />
      </div>

      <ValueInput index={1} value={value1} onChange={onValueChange} />
      <ValueInput index={2} value={value2} onChange={onValueChange} />

      <br />
      <div className={"flexRow"}>
        <div>and amount</div>
        <Select
          items={LESS_MORE}
          selected={lessMoreIndex}
          onChange={onLessMoreChange}
        />
        <NumberInput
          value={lessMoreAmount}
          min={0}
          decimalPoints={2}
          width={"short"}
          onChange={onLessMoreAmountChange}
        />
        <div>(0 : ignored)</div>
      </div>
    </div>
  );
};

export default NewRuleTable;
