import { Json } from "core/types";
import { URL } from "./constants";
import { fetchUrl, postJson } from "./utils";
import { removeNullsFromObject } from "core/utils";
import testdata from "testdata/rules.json";

export const fetchRules = async (cloud: boolean = false) => {
  let rules: any;

  if (cloud) {
    rules = testdata;
  } else {
    rules = await fetchUrl(URL.RULES);
  }

  return rules;
};

export const addRule = (dataJson: Json) => {
  const nullsRemoved = removeNullsFromObject(dataJson);
  return postJson(nullsRemoved, URL.RULES);
};

