import React from "react";
import { useHistory } from "react-router-dom";
import { useWindowSize } from "hooks";
import { TransactionClass } from "core/TransactionClass";
import { Field } from "components";
import { splitLongStringAt } from "core/utils";
import cn from "classnames";
import s from "./Transaction.module.css";

type Props = {
  index: number;
  transaction: TransactionClass;
  clickable: boolean;
  urlId: number;
  isHashVisible: boolean;
  isMasked: boolean;
};

// ----------
// COMPONENT:
const Transaction = ({
  index,
  transaction,
  clickable,
  urlId,
  isHashVisible,
  isMasked,
}: Props) => {
  // HOOKS:
  const { desktop, mobile } = useWindowSize();
  const history = useHistory();

  // FUNCTIONS:
  const fieldString = (
    name: string,
    value: string,
    splitAt: number = 0,
    iconOnly: boolean = false
  ) => (
    <Field
      name={name}
      valueString={splitAt === 0 ? value : splitLongStringAt(value, splitAt)}
      isMasked={isMasked}
      iconOnly={iconOnly}
    />
  );
  // --------------------------------------------------------------
  const fieldStringUnderlined = (
    name: string,
    transaction: TransactionClass,
    splitAt: number = 0
  ) => {
    const fieldValue = transaction.getFieldValueString(name);
    const { rule } = transaction;
    const ruleField = rule ? rule.field.toLowerCase() : null;

    if (rule && ruleField === name) {
      const { operation, caseSensitive, value1 } = rule;

      if (operation === "= v1") {
        return (
          <Field
            name={name}
            valueStringMiddle={fieldValue}
            isMasked={isMasked}
          />
        );
      }

      if (operation === "includes v1") {
        const fv =
          caseSensitive === "Yes" ? fieldValue : fieldValue.toLowerCase();
        const v1 = caseSensitive === "Yes" ? value1 : value1.toLowerCase();
        const i = fv.indexOf(v1);

        return (
          <Field
            name={name}
            valueStringBefore={fieldValue.slice(0, i)}
            valueStringMiddle={fieldValue.slice(i, i + v1.length)}
            valueStringAfter={fieldValue.slice(i + v1.length)}
            isMasked={isMasked}
          />
        );
      }
    }

    return (
      <Field
        name={name}
        valueString={
          splitAt === 0 ? fieldValue : splitLongStringAt(fieldValue, splitAt)
        }
        isMasked={isMasked}
      />
    );
  };
  // --------------------------------------------------------------
  const fieldNumber = (name: string, fieldValue: number) => (
    <Field name={name} valueNumber={fieldValue} isMasked={isMasked} />
  );

  const isIncoming = transaction.isIncoming;
  const incomingClass = isIncoming ? s.incoming : "";
  const clickableClass = clickable ? s.clickable : "";
  const highlightIndexClass =
    urlId === transaction.index ? s.highlightIndex : "";

  const handleClick = clickable
    ? (index: number) => {
        history.replace(`/rules/transaction=${index}`);
      }
    : () => {};

  return (
    <tr
      className={cn("root", incomingClass, clickableClass, highlightIndexClass)}
      onClick={() => handleClick(transaction.index)}
    >
      {desktop && fieldNumber("index", transaction.index)}
      {fieldString(index === 0 ? "date" : "dateShort", transaction.date)}
      {fieldStringUnderlined("name", transaction)}
      {desktop && fieldString("counterAccount", transaction.counterAccount, 8)}
      {desktop && fieldString("code", transaction.code)}
      {fieldNumber("amount", transaction.amount)}
      {desktop && fieldString("type", transaction.type)}
      {desktop && fieldStringUnderlined("remarks", transaction)}
      {isHashVisible && fieldString("hash", transaction.hash.toString(), 6)}
      {fieldString("category", transaction.category, 0, mobile)}
    </tr>
  );
};

export default Transaction;
