import React, { useState } from "react";
import { useWindowSize } from "hooks";
import { DesktopMenu, Icon, MobileMenu, PageHeaderButtons } from "components";
import cn from "classnames";
import s from "./PageHeader.module.css";

type Props = {
  cloud: boolean;
  onCloudClick: () => void;
  mask: boolean;
  onMaskClick: () => void;
};

// ----------
// COMPONENT:
const PageHeader = ({ cloud, onCloudClick, mask, onMaskClick }: Props) => {
  // HOOKS:
  const { desktop, mobile } = useWindowSize();

  // STATE:
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  // FUNCTIONS:
  const onMenuIconClick = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  return (
    <>
      <header className={cn(s.root, "flexRowWithMarginBaseline", s.stickyTop)}>
        {mobile && <Icon name="menu" onClick={onMenuIconClick} />}

        {isMobileMenuOpen && <MobileMenu onMenuIconClick={onMenuIconClick} />}

        <a
          className={s.appTitle + " flexRowLeft"}
          href="https://eyng.github.io"
        >
          <img className={s.logo} alt="" src="/favicon-32x32.png" />
          <div>ELSBNK</div>
        </a>

        {desktop && <DesktopMenu />}

        <PageHeaderButtons
          cloud={cloud}
          onCloudClick={onCloudClick}
          mask={mask}
          onMaskClick={onMaskClick}
        />
      </header>

      <div className={s.stickyBottom} />
    </>
  );
};

export default PageHeader;
