import React from "react";
import cn from "classnames";
import s from "./ValueInput.module.css";

type Props = {
  index: number;
  value: string;
  onChange: (event: any, index: number) => void;
};

// ----------
// COMPONENT:
const ValueInput = ({ index, value, onChange }: Props) => (
  <div className={cn(s.root, "flexRowWithMargin")}>
    <div className={s.valueTitle}>Value {index}:</div>
    <input
      type="text"
      value={value}
      onChange={event => onChange(event, index)}
    />
  </div>
);

export default ValueInput;
