import React from "react";
import { processField } from "process/field";
import { CategoryIconAndLabel } from "components";
import s from "./Field.module.css";

type Props = {
  name: string;
  valueStringBefore?: string;
  valueStringMiddle?: string;
  valueStringAfter?: string;
  valueString?: string;
  valueNumber?: number;
  isMasked: boolean;
  iconOnly?: boolean;
};

// ----------
// COMPONENT:
const Field = ({
  name,
  valueStringBefore,
  valueStringMiddle,
  valueStringAfter,
  valueString,
  valueNumber,
  isMasked,
  iconOnly = false,
}: Props) => {
  // CATEGORY COLUMN:
  if (name === "category") {
    return valueString ? (
      <td>
        <CategoryIconAndLabel
          item={processField(valueString, name)}
          fullWidth={false}
          iconOnly={iconOnly}
        />
      </td>
    ) : (
      <td className={s.categoryEmpty}></td>
    );
  }

  // OTHER COLUMNS:
  const textAlign = name === "amount" ? "textAlignRight" : "";
  if (valueString) {
    return (
      <td className={textAlign}>{processField(valueString, name, isMasked)}</td>
    );
  }
  if (valueStringMiddle) {
    return isMasked ? (
      <td className={textAlign}>
        {processField(
          valueStringBefore + valueStringMiddle + valueStringAfter,
          name,
          isMasked
        )}
      </td>
    ) : (
      <td className={textAlign}>
        {valueStringBefore && <span>{valueStringBefore}</span>}
        <span className={s.highlight}>
          {processField(valueStringMiddle, name, isMasked)}
        </span>
        {valueStringAfter && <span>{valueStringAfter}</span>}
      </td>
    );
  }
  if (valueNumber) {
    return (
      <td className={textAlign}>
        {processField(valueNumber.toString(), name, isMasked)}
      </td>
    );
  }

  return <td></td>;
};

export default Field;
