import { Rule } from "../core/types";
import { TransactionClass } from "core/TransactionClass";
import { getCategoryRule } from "./categories";

const extendTransaction = (
  transaction: TransactionClass,
  index: number,
  rule: Rule | null
): TransactionClass =>
  rule
    ? {
        ...transaction,
        index: index + 1,
        rule,
        category: rule.category,
      }
    : {
        ...transaction,
        index: index + 1,
        category: "category-not-found-3",
      };

export const extendTransactions = (
  transactions: TransactionClass[],
  start: number,
  rules: Rule[]
): TransactionClass[] => {
  return transactions.map((transaction, index) => {
    const rule = getCategoryRule(transaction, rules);

    return extendTransaction(transaction, start + index, rule);
  });
};
