export const getColour = (index: number) =>
  index >= COLOURS.length ? "" : `#${COLOURS[index]}`;

export const getCategoryColour = (
  CATEGORIES: string[],
  category: string
): string => {
  if (!category || category === "Category") {
    console.error("*** category:", category);
    return "colour-not-found";
  }
  const categoryIndex = CATEGORIES.indexOf(category);
  const colour = getColour(categoryIndex) || "colour-not-found";
  return colour;
};
