import React, { useState, useEffect, useContext } from "react";
import { AppContext, AppContextType } from "appContext";
import { NotificationType, Severity } from "core/types";
import { Notification } from "components";
import s from "./Notifications.module.css";

const MaxNotifications = 3;
const MaxSecondsLeft = 10;

const DefaultNotifications: NotificationType[] = [
  // {
  //   severity: "info",
  //   message: "Information",
  //   secondsLeft: 5,
  // },
  // {
  //   severity: "debug",
  //   message: "Debug",
  //   secondsLeft: 4,
  // },
  // {
  //   severity: "warning",
  //   message: "Warning",
  //   secondsLeft: 3,
  // },
  // {
  //   severity: "error",
  //   message: "Error",
  //   secondsLeft: 2,
  // },
];

// ----------
// COMPONENT:
const Notifications = () => {
  // CONTEXT:
  const context: AppContextType = useContext(AppContext);

  // STATE:
  const [notifications, setNotifications] = useState(DefaultNotifications);

  // EFFECTS:
  useEffect(
    () => {
      const interval = setInterval(everySecond, 1000);
      return () => clearInterval(interval);
    } /* ,[] */
  );

  // FUNCTIONS:
  const everySecond = () => {
    const decremented = notifications.map(n => ({
      ...n,
      secondsLeft: n.secondsLeft - 1,
    }));
    const filtered = decremented.filter(n => n.secondsLeft > 0);
    setNotifications(filtered);
  };

  context.addNotification = (severity: Severity, message: string): void => {
    const notification: NotificationType = {
      severity,
      message,
      secondsLeft: MaxSecondsLeft,
    };
    let newArray = [...notifications, notification];
    if (newArray.length > MaxNotifications) {
      newArray.shift();
    }
    setNotifications(newArray);
  };

  // CLICKS:
  const onClearButtonClick = () => {
    setNotifications([]);
  };

  return (
    <div className={s.root}>
      <div>
        {notifications.map((notification: NotificationType, index: number) => (
          <Notification key={index} notification={notification} />
        ))}
      </div>
      {notifications.length > 0 && (
        <div>
          <button onClick={onClearButtonClick}>Clear</button>
        </div>
      )}
    </div>
  );
};

export default Notifications;
