import React from "react";
import { Icon } from "components";
import s from "./IconButton.module.css";

type Props = {
  icon: string;
  text: string;
  onClick: null | (() => void);
};

// ----------
// COMPONENT:
const IconButton = ({ icon: iconName, text, onClick }: Props) => {
  const handleClick = () => {
    if (onClick) {
      onClick();
    }
  };

  return (
    <button className={s.root + " flexRowLeft"} onClick={handleClick}>
      <Icon name={iconName} />
      <div>{text}</div>
    </button>
  );
};

export default IconButton;
