import { useContext } from "react";
import { AppContext, AppContextType } from "appContext";

const useNotifications = () => {
  const { addNotification }: AppContextType = useContext(AppContext);

  const info = (message: string): void => addNotification("info", message);
  const debug = (message: string): void => addNotification("debug", message);
  const warning = (message: string): void =>
    addNotification("warning", message);
  const error = (message: string): void => addNotification("error", message);

  return { /*addNotification,*/ info, debug, warning, error };
};

export default useNotifications;
