import React from "react";
import s from "./ToggleButton.module.css";

type Props = {
  on: boolean;
  text: string;
  // textFalse: string;
  // iconTrue: string;
  // iconFalse: string;
  onClick: (event: any) => void;
};

// ----------
// COMPONENT:
const ToggleButton = ({ on, text, onClick }: Props) => {
  return (
    <div className={s.root}>
      <button className={on ? s.on : ""} onClick={onClick}>
        {text}
      </button>
    </div>
  );
};

export default ToggleButton;
