import React from "react";
import { Icon } from "components";
import s from "./PageHeaderButtons.module.css";

type Props = {
  cloud: boolean;
  onCloudClick: () => void;
  mask: boolean;
  onMaskClick: () => void;
};

// ----------
// COMPONENT:
const PageHeaderButtons = ({
  cloud,
  onCloudClick,
  mask,
  onMaskClick,
}: Props) => {
  return (
    <div className={s.root + " flexRowLeft"}>
      <button onClick={onCloudClick}>
        <Icon name={cloud ? "cloudOn" : "cloudOff"} />
      </button>
      <button onClick={onMaskClick}>
        <Icon name={mask ? "maskOn" : "maskOff"} />
      </button>
    </div>
  );
};

export default PageHeaderButtons;
