import { Rule } from "core/types";
import { TransactionClass } from "core/TransactionClass";
import {
  group,
  getInitialCategoryFilterJs,
  getTotalsByCategory,
  spread,
  toggleCategoryFilterJs
} from "./trends_js.js";
import { getCategory } from "./categories";

type TrendsChartData = any[];

const mapTransactions = (transaction: TransactionClass, rules: Rule[]) => {
  const { amount, date, category } = transaction;
  const month = date.substring(2, 6); // yymm

  return {
    index: `${month}_${category}`,
    value: amount / 100
  };
};

const addCategoriesToTransactions = (
  transactions: TransactionClass[],
  rules: Rule[]
) => {
  transactions.forEach(t => (t.category = getCategory(t, rules)));
};

const getChartData = (transactions: TransactionClass[], rules: Rule[]): [] => {
  const mapped = transactions.map(t => mapTransactions(t, rules));
  const grouped = {};
  mapped.forEach(item => group(item, grouped));

  const groupedByMonth = {};
  Object.entries(grouped).forEach(item => spread(item, groupedByMonth));
  return Object.values(groupedByMonth) as [];
};

export const getCategoriesFromChartData = (chartData: any[]): string[] => {
  const groupedArray = getTotalsByCategory(chartData);
  return groupedArray.map(item => item[0]);
};

export const getTrendsChartData = (
  transactions: TransactionClass[],
  rules: Rule[]
): TrendsChartData => {
  console.log("*** GETTRENDSCHARTDATA CALLED");
  addCategoriesToTransactions(transactions, rules);

  const chartData: TrendsChartData = getChartData(transactions, rules);
  return chartData;
};

export const getInitialCategoryFilter = (categories: string[]): Object =>
  getInitialCategoryFilterJs(categories);

export const toggleCategoryFilter = (
  categoryFilter: object,
  category: string
) => {
  return toggleCategoryFilterJs(categoryFilter, category);
};
