import React from "react";
import cn from "classnames";
import { SELECT_ARRAYS } from "core/constants";
import { getCategoryColour } from "core/colours";
import s from "./CategoryLegend.module.css";

const { CATEGORIES } = SELECT_ARRAYS;

type OnClickType = (category: string) => void;

// ----------
// COMPONENT:
const Category = (
  category: string,
  onClick: OnClickType,
  categoriesFiltered: string[]
) => {
  const isSelected = categoriesFiltered.includes(category);
  const selectedClass = isSelected ? s.selected : "";
  const categoryColor = getCategoryColour(CATEGORIES, category);
  const style = isSelected ? { backgroundColor: categoryColor } : {};

  return (
    <div
      key={category}
      className={cn(s.categoryColour, selectedClass)}
      style={style}
      onClick={() => onClick(category)}
    >
      {category}
    </div>
  );
};

type Props = {
  categories: string[];
  categoriesFiltered: string[];
  className: string;
  onClick: OnClickType;
};

// ----------
// COMPONENT:
const CategoryLegend = ({
  categories,
  categoriesFiltered,
  className,
  onClick
}: Props) => {
  return (
    <div className={s.root}>
      <div className={className}>
        {categories.map(c => Category(c, onClick, categoriesFiltered))}
      </div>
    </div>
  );
};

export default CategoryLegend;
