import React from "react";
import { useWindowSize } from "hooks";
import { TransactionClass } from "core/TransactionClass";
import { Transaction, TransactionsTableHeader } from "components";
import s from "./TransactionList.module.css";

type Props = {
  transactions: TransactionClass[];
  clickable: boolean;
  urlId: number;
  isHashVisible: boolean;
  isMasked: boolean;
};

// ----------
// COMPONENT:
const TransactionList = ({
  transactions,
  clickable,
  urlId,
  isHashVisible,
  isMasked,
}: Props) => {
  // HOOKS:
  const { mobile } = useWindowSize();

  return (
    <div className={s.root}>
      {transactions.length === 1 && <h3>Transaction</h3>}

      <table className={mobile ? s.tableMobile : s.tableDesktop}>
        <TransactionsTableHeader isHashVisible={isHashVisible} />

        <tbody>
          {transactions.map((t, index) => (
            <Transaction
              key={index}
              index={index}
              transaction={t}
              clickable={clickable}
              urlId={urlId}
              isHashVisible={isHashVisible}
              isMasked={isMasked}
            />
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default TransactionList;
