import { URL } from "./constants";
import { TransactionClass } from "core/TransactionClass";
import { fetchUrl } from "./utils";
import testdata from "testdata/transactions.json";
// import data from "./ing.json";

// ------------------------
// Datum, Naam / Omschrijving, Rekening, Tegenrekening,
// Code, Af Bij, Bedrag (EUR), MutatieSoort, Mededelingen
export const fetchTransactions = async (
  start: number,
  end: number,
  cloud: boolean = false
) => {
  let transactions: string[][];

  if (cloud) {
    transactions = testdata;
  } else {
    transactions = await fetchUrl(
      URL.TRANSACTIONS + `?_start=${start}&_end=${end}`
    );
  }

  return transactions.map(t => new TransactionClass(t));

  // try {
  //   return transactions.map(t => new TransactionClass(t));
  // } catch (error) {
  //   return [];
  // }
};
