import { FIELD } from "core/constants";
import { Rule } from "./types";
import { getHash } from "./utils";

export class TransactionClass {
  index: number; // added
  date: string;
  name: string;
  // account: string;
  counterAccount: string;
  code: string;
  isIncoming: boolean;
  amount: number; // 123,45 converted to 12345 (cents)
  type: string;
  remarks: string;
  rule?: Rule; // added
  category: string; // added
  hash: number; // added

  constructor(data: string[]) {
    if (data[FIELD.ACCOUNT] !== "NL71INGB0004410690") {
      console.error("*** ERROR: account:", data[FIELD.ACCOUNT]);
    }

    this.index = 0;
    this.date = data[FIELD.DATE].toString();
    this.name = data[FIELD.NAME].toString();
    // this.account = "";
    this.counterAccount = data[FIELD.COUNTER_ACCOUNT].toString();
    this.code = data[FIELD.CODE].toString();
    this.isIncoming = data[FIELD.INCOMING] === "Bij";
    this.amount = parseFloat(data[FIELD.AMOUNT].replace(",", ""));
    this.type = data[FIELD.TYPE].toString();
    this.remarks = data[FIELD.REMARKS].toString();
    this.category = ""; // todo: remove later, use rule.category
    this.hash = getHash(data.join(","));

    // post process:
    if (!this.isIncoming) {
      this.amount = -1 * this.amount;
    }
  }

  getFieldValueString = (fieldName: string): string => {
    switch (fieldName.toLowerCase()) {
      case "index":
        return this.index.toString();
      case "date":
        return this.date;
      case "name":
        return this.name;
      // case "Account":
      //   return this.account;
      case "counter account":
        return this.counterAccount;
      case "code":
        return this.code;
      case "incoming":
        return this.isIncoming ? "Bij" : "Af";
      case "amount":
        return this.amount.toString();
      case "type":
        return this.type;
      case "remarks":
        return this.remarks;
      case "category":
        return this.category;
      case "hash":
        return this.hash.toString();
      default:
        return "field-not-found";
    }
  };
}
