import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { Match } from "core/types";
import { TransactionClass } from "core/TransactionClass";
import { getIdFromMatch } from "core/utils";
import { NewRule, TransactionList } from "components";
import { fetchTransactions } from "services/transactions";
import { extendTransactions } from "process/transactions";
import { fetchRules } from "services/rules";
import s from "./RulesPage.module.css";

const TransactionsDefault: TransactionClass[] = [];

type Props = {
  match: Match;
  isMasked: boolean;
};

// ----------
// COMPONENT:
const RulesPage = ({ match, isMasked }: Props) => {
  const history = useHistory();

  // STATE:
  const [transactions, setTransactions] = useState(TransactionsDefault);
  const [rules, setRules] = useState([]);
  const id = getIdFromMatch(match);

  // EFFECTS - GET DATA:
  useEffect(() => {
    const fetchData = async () => {
      const r = await fetchRules();
      setRules(r);

      if (id) {
        const index = id - 1;
        const t = await fetchTransactions(index, index + 1);
        const extended = extendTransactions(t, index, r);
        setTransactions(extended);
      }
    };
    fetchData();

    return () => {}; // on unmount
  }, [id, match]);

  // CLICKS:
  const onSaveClick = () => {
    history.replace(`/transactions/transaction=${id}`);
  };

  return (
    <div className={s.root}>
      {transactions && transactions.length > 0 && (
        <>
          <div className={s.box}>
            <TransactionList
              transactions={transactions}
              clickable={false}
              urlId={0}
              isHashVisible={true}
              isMasked={isMasked}
            />
          </div>
          <div className={s.box}>
            <NewRule transaction={transactions[0]} onSaveClick={onSaveClick} />
          </div>
        </>
      )}

      <h3>Rules</h3>
      <div>
        {rules.map((rule, index) => (
          <div key={index}>{Object.values(rule).join(", ")}</div>
        ))}
      </div>
    </div>
  );
};

export default RulesPage;
