import React, { useState } from "react";
import { CategoryIconAndLabel } from "components";
import cn from "classnames";
import s from "./SelectWithIcons.module.css";

type Props = {
  items: string[];
  selected: number;
  onChange: (event: any) => void;
};

// ----------
// COMPONENT:
const SelectWithIcons = ({ items, selected, onChange }: Props) => {
  const [expanded, setExpanded] = useState(false);

  return (
    <div className={s.root} onClick={() => setExpanded(!expanded)}>
      <div className={s.topBar}>
        <div className={s.topBarText}>{items[selected]}</div>
        <div className={s.topBarButton} />
      </div>

      {expanded && (
        <div className={s.items}>
          {items.map((item, index) => (
            <div key={index} className={s.item}>
              <div
                className={cn(s.selectMarker, {
                  [s.selected]: index === selected,
                })}
              />
              <CategoryIconAndLabel
                index={index}
                fullWidth={true}
                item={item}
                onChange={onChange}
              />
              {/* <div
                className={s.iconAndLabel}
                style={{ backgroundColor: getCategoryColour(CATEGORIES, item) }}
                onClick={() => onChange(index)}
              >
                <span className={s.itemIcon}>
                  <CategoryIcon category={getCategoryIcon(item)} />
                </span>
                {item}
              </div> */}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default SelectWithIcons;
