import { CATEGORIES, CATEGORIES_IGNORED_DEFAULT } from "core/constants";

// Generic function
export const sortArrayByMember = (memberIndex, a, b) =>
  a[memberIndex] - b[memberIndex];

export const groupByCategory = (item, groupedArrayObject) => {
  const [category, amount] = item;
  const currentAmount = groupedArrayObject[category];
  groupedArrayObject[category] = currentAmount
    ? currentAmount + amount
    : amount;
};

export const getTotalsByCategory = chartData => {
  const groupedArrayObject = {};
  const months = Object.values(chartData);

  months.forEach(month => {
    const entriesExceptName = Object.entries(month).filter(
      item => item[0] !== "name"
    );
    entriesExceptName.forEach(entry =>
      groupByCategory(entry, groupedArrayObject)
    );
  });

  const groupedArray = Object.entries(groupedArrayObject);
  groupedArray.sort((a, b) => sortArrayByMember(1, a, b));
  groupedArray.reverse();
  return groupedArray;
};

export const group = (item, groupedArrayObject) => {
  const { index, value } = item;
  const currentValue = groupedArrayObject[index];
  groupedArrayObject[index] = currentValue ? currentValue + value : value;
};

export const spread = (item, groupedByMonth) => {
  const [index, value] = item;
  const [month, category] = index.split("_");

  if (!groupedByMonth[month]) {
    groupedByMonth[month] = { name: month };
  }

  groupedByMonth[month][category] = -value;
};

export const getCategoryFilterDefault = max => {
  const objectArray = {};
  let count = 0;

  CATEGORIES.forEach(category => {
    if (count < max && !CATEGORIES_IGNORED_DEFAULT.includes(category)) {
      objectArray[category] = true;
      count += 1;
    } else {
      objectArray[category] = false;
    }
  });

  return objectArray;
};

export const getInitialCategoryFilterJs = largestCategories => {
  const max = 5; // 7
  const objectArray = {};

  CATEGORIES.forEach(category => {
    objectArray[category] = !CATEGORIES_IGNORED_DEFAULT.includes(category);
  });

  const largestCategoriesNotIgnored = largestCategories.filter(
    c => !CATEGORIES_IGNORED_DEFAULT.includes(c)
  );

  for (let i = max; i < largestCategoriesNotIgnored.length; i += 1) {
    objectArray[largestCategoriesNotIgnored[i]] = false;
  }

  return objectArray;
};

export const applyFilter = (categories, categoryFilter) => {
  const largestCategoriesFiltered = categories.filter(c => categoryFilter[c]);
  return largestCategoriesFiltered;
};

export const toggleCategoryFilterJs = (categoryFilter, category) => {
  const objectArray = {};
  Object.entries(categoryFilter).forEach(f => {
    objectArray[f[0]] = f[0] === category ? !f[1] : f[1];
  });
  return objectArray;
};
