import React from "react";
import { TrendChartPageHeaderButtons } from "components";
import cn from "classnames";
import s from "./TrendChartPageHeader.module.css";

type Props = {
  isStacked: boolean;
  isBoxed?: boolean;
  onStackedClick: () => void;
};

// ----------
// COMPONENT:
const TrendChartPageHeader = ({
  isStacked,
  isBoxed = false,
  onStackedClick,
}: Props) => {
  const isFullPage = !isBoxed;
  const title = "Trend: expense categories";

  return (
    <div className={cn(s.root, "flexRowWithMargin")}>
      {isFullPage ? <h3>{title}</h3> : <h4>{title}</h4>}

      {isFullPage && (
        <TrendChartPageHeaderButtons
          isStacked={isStacked}
          onStackedClick={onStackedClick}
        />
      )}
    </div>
  );
};

export default TrendChartPageHeader;
