import React, { useState } from "react";
import cn from "classnames";
import s from "./NumberInput.module.css";

type Props = {
  value: string;
  min?: number;
  max?: number;
  decimalPoints?: number;
  width?: "short";
  onChange: (value: string) => void;
};

// ----------
// COMPONENT:
const NumberInput = ({
  value,
  min,
  max,
  decimalPoints,
  width,
  onChange,
}: Props) => {
  const [stateValue, setStateValue] = useState(value);

  const validateNumber = (input: string) => {
    let n = Number(input);
    if (!n) return null;

    if (decimalPoints) {
      n = Math.floor(n * 100 ** decimalPoints) / 100 ** decimalPoints;
    }

    if (min && min !== 0 && n < min) n = min;
    if (max && max !== 0 && n > max) n = max;

    return decimalPoints ? n.toFixed(decimalPoints) : n.toString();
  };

  const handleChange = (event: any) => {
    setStateValue(event.target.value);
  };

  const handleBlur = (event: any) => {
    const s: string = validateNumber(event.target.value) || value;
    setStateValue(s);
    onChange(s);
  };

  return (
    <input
      type="text"
      className={cn(s.root, { [s.short]: width === "short" })}
      placeholder={"0.00"}
      value={stateValue}
      onChange={handleChange}
      onFocus={() => setStateValue("")}
      onBlur={handleBlur}
    />
  );
};

export default NumberInput;
