import { Match } from "core/types";

// 12345 --> 123.45
export const centsToDecimal = (cents: string): string =>
  cents.replace("-", "").length < 3
    ? `${cents}¢`
    : `${cents.slice(0, -2)}.${cents.slice(-2)}`;

export const getIdFromMatch = (match: Match): number => {
  if (!match) return 0;
  const { params } = match;
  const { id } = params;
  return parseInt(id);
};

// https://stackoverflow.com/questions/7616461/generate-a-hash-from-string-in-javascript
export const getHash = (s: string) =>
  s.split("").reduce((a, b) => {
    a = (a << 5) - a + b.charCodeAt(0);
    return a & a;
  }, 0);

export const getObjectValueString = (object: object, key: string): string => {
  const item = Object.entries(object).find(item => item[0] === key);
  return item ? item[1] : null;
};

