const splitStringAt = (s: string, position: number): string => {
  return s.slice(0, position) + " " + s.slice(position);
};

export const splitLongStringAt = (s: string, position: number): string => {
  return s && s.length > 5 ? splitStringAt(s, position) : s;
};

export const toSentenceCase = (s: string) => {
  return s[0].toUpperCase() + s.slice(1).toLowerCase();
};

const MONTHS = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "Jun",
  "Jul",
  "Aug",
  "Sep",
  "Oct",
  "Nov",
  "Dec",
];

const parse20191231 = (dateString: string): [number, number, number] => {
  var year = parseInt(dateString.substring(0, 4));
  var month = parseInt(dateString.substring(4, 6));
  var day = parseInt(dateString.substring(6, 8));
  return [year, month, day];
};

const convert20191231toDate = (dateString: string) => {
  const [year, month, day] = parse20191231(dateString);
  return new Date(year, month - 1, day);
};

export const daysBetween = (fromString: string, toString: string) => {
  const milliseconds =
    convert20191231toDate(toString).getTime() -
    convert20191231toDate(fromString).getTime();
  return Math.round(milliseconds / (1000 * 60 * 60 * 24));
};

export const convert20191231to31_Dec_2019 = (dateString: string) => {
  const [year, month, day] = parse20191231(dateString);
  var monthMMM = MONTHS[month - 1];
  return `${day} ${monthMMM} ${year}`;
};
