import React, { useEffect } from "react";
import { Icon } from "components";
import cn from "classnames";
import s from "./TransactionsPageHeaderButtons.module.css";

type Event = {
  key: string;
};

type Props = {
  pageIndex: number;
  onClick: (event: any) => void;
};

// ----------
// COMPONENT:
const TransactionsPageHeaderButtons = ({ pageIndex, onClick }: Props) => {
  const percentage = (pageIndex * 1000) / 11636;

  // EFFECTS - GET DATA:
  useEffect(() => {
    document.addEventListener("keydown", handleKeyDown);

    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    }; // on unmount
  });

  const handleKeyDown = (event: Event) => {
    const { key } = event;

    switch (key) {
      case "Home":
        onClick(0);
        break;

      case "ArrowLeft":
        onClick(-1);
        break;

      case "ArrowRight":
        onClick(1);
        break;

      default:
        break;
    }
  };

  return (
    <div className={cn(s.root, "flexRowWithMargin")}>
      <button onClick={() => onClick(0)}>
        <Icon name="first" />
      </button>

      <button onClick={() => onClick(-1)}>
        <Icon name="previous" />
      </button>

      <div>
        {pageIndex + 1} ({percentage.toFixed(1)}%)
      </div>

      <button onClick={() => onClick(1)}>
        <Icon name="next" />
      </button>
    </div>
  );
};

export default TransactionsPageHeaderButtons;
