import { Rule } from "core/types";
import { TransactionClass } from "core/TransactionClass";
import { convert20191231to31_Dec_2019, daysBetween } from "core/utils";
import { getTotal } from "core/utils_js.js";
import { getCategory } from "./categories";
import { groupCategoryAndCents } from "./charts_js.js";

type CategoryAndCents = [string, number];

type GraphDataItem = {
  name: string;
  value: number;
  label: string;
};

type ExpensesByCategoryGraphData = {
  graphDataItems: GraphDataItem[];
  total: number;
  from: string;
  to: string;
  dayCount: number;
  totalPerMonth: number;
};

const sortCategoriesAndCents = (a: CategoryAndCents, b: CategoryAndCents) => {
  const m = a[1];
  const n = b[1];
  return m - n;
};

const getDateRange = (transactions: TransactionClass[]) => {
  const fromString = transactions[transactions.length - 1].date;
  const toString = transactions[0].date;
  const from = convert20191231to31_Dec_2019(fromString);
  const to = convert20191231to31_Dec_2019(toString);
  const dayCount = daysBetween(fromString, toString);
  return { from, to, dayCount };
};

const perMonth = (amount: number, days: number) =>
  Math.round(((amount / days / 100) * 365.25) / 12);

const getGraphDataItem = (
  item: CategoryAndCents,
  days: number,
  total: number
): GraphDataItem => {
  const name = item[0];
  const value = item[1];
  const valueToDisplay = perMonth(value, days);
  const percentage = (value * 100) / total;
  const label =
    percentage >= 1
      ? `${name} ${valueToDisplay} (${percentage.toFixed(1)}%)`
      : "";
  return { name, value, label };
};

const filterCategoryAndCents = (item: CategoryAndCents) =>
  item[0] !== "Credit card" &&
  item[0] !== "Ignore" &&
  item[0] !== "Salary" &&
  item[0] !== "Self";

const getCategoryAndCents = (
  transaction: TransactionClass,
  rules: Rule[]
): CategoryAndCents => {
  const category = getCategory(transaction, rules);
  return [category, -transaction.amount];
};

const groupCategoryAndCentsArray = (
  categoryAndCentsArray: CategoryAndCents[]
): object => {
  let groupedAsObject = {};
  categoryAndCentsArray.forEach(item =>
    groupCategoryAndCents(item, groupedAsObject)
  );
  return groupedAsObject;
};

export const getExpensesByCategoryGraphData = (
  transactions: TransactionClass[],
  rules: Rule[]
): ExpensesByCategoryGraphData => {
  console.log("*** GETEXPENSESBYCATEGORYGRAPHDATA CALLED");

  const categoryAndCentsArray: CategoryAndCents[] = transactions.map(
    transaction => getCategoryAndCents(transaction, rules)
  );

  const filtered = categoryAndCentsArray.filter(item =>
    filterCategoryAndCents(item)
  );

  const grouped = groupCategoryAndCentsArray(filtered);
  const groupedArray = Object.entries(grouped);
  groupedArray.sort(sortCategoriesAndCents);

  // const reducer = (total: number, item: CategoryAndCents) => total + item[1];
  // const total = groupedArray.reduce(reducer, 0);
  const total = getTotal(groupedArray, "1");

  const { from, to, dayCount } = getDateRange(transactions);

  const graphDataItems: GraphDataItem[] = groupedArray.map(item =>
    getGraphDataItem(item, dayCount, total)
  );

  const totalPerMonth = perMonth(total, dayCount);

  return {
    graphDataItems,
    total: Math.round(total / 100),
    from,
    to,
    dayCount,
    totalPerMonth,
  };
};
