import React from "react";
import { Severity, VoidFunction } from "core/types";

export type AppContextType = {
  addNotification: (severity: Severity, message: string) => void;
  openModal: (component: JSX.Element, onYesClick: VoidFunction) => void;
};

export const DefaultContext: AppContextType = {
  addNotification: () => {},
  openModal: () => {},
};

export const AppContext = React.createContext(DefaultContext);
