import React, { useState, useEffect } from "react";
import { useWindowSize } from "hooks";
import { TRANSACTION_COUNT } from "core/constants";
import { fetchRules } from "services/rules";
import { fetchTransactions } from "services/transactions";
import {
  getTrendsChartData,
  getCategoriesFromChartData,
  getInitialCategoryFilter,
  toggleCategoryFilter,
} from "process/trends";
import { applyFilter } from "process/trends_js.js";
import {
  AreaChartStacked,
  AreaChartNonstacked,
  CategorySelector,
  Loading,
  TrendChartPageHeader,
} from "components";
import cn from "classnames";
import s from "./TrendChartPage.module.css";

import testdata from "testdata/trendsChartData.json";

const CATEGORIES_DEFAULT: string[] = [];
const TRENDS_CHART_DATA_DEFAULT: any[] = [];
const CATEGORY_FILTER_DEFAULT: object = {};

type Props = {
  isBoxed?: boolean;
  cloud: boolean;
  onBoxClick?: (target: string) => void;
};

// ----------
// COMPONENT:
const TrendChartPage = ({
  isBoxed = false,
  cloud,
  onBoxClick = () => {},
}: Props) => {
  // HOOKS:
  const { mobile } = useWindowSize();

  const isFullPage = !isBoxed;
  const factor = isFullPage ? 1 : 0.6;

  // STATE:
  const [trendsChartData, setTrendsChartData] = useState(
    TRENDS_CHART_DATA_DEFAULT
  );
  const [categories, setCategories] = useState(CATEGORIES_DEFAULT);
  const [isStacked, setIsStacked] = useState(true);
  const [categoryFilter, setCategoryFilter] = useState(CATEGORY_FILTER_DEFAULT);
  const [categoriesFiltered, setCategoriesFiltered] = useState([]);

  // EFFECTS - GET DATA:
  useEffect(() => {
    const fetchData = async () => {
      const r = await fetchRules(cloud);
      const t = await fetchTransactions(0, TRANSACTION_COUNT, cloud);

      const cd = cloud ? testdata : getTrendsChartData(t, r);

      const c = getCategoriesFromChartData(cd);
      const f: object = getInitialCategoryFilter(c);

      setTrendsChartData(cd);
      setCategories(c);
      setCategoryFilter(f);
    };
    fetchData();
    return () => {}; // on unmount
  }, [cloud]); // Only once

  // EFFECTS - CATEGORY FILTER:
  useEffect(() => {
    const fetchData = async () => {
      const filtered = applyFilter(categories, categoryFilter);
      setCategoriesFiltered(filtered);
    };
    fetchData();
    return () => {}; // on unmount
  }, [categories, categoryFilter]);

  // CLICKS:
  const onCategoryClick = (category: string): void => {
    const cf = toggleCategoryFilter(categoryFilter, category);
    setCategoryFilter(cf);
  };
  const onStackedClick = (): void => {
    setIsStacked(!isStacked);
  };

  if (isFullPage && trendsChartData.length === 0) {
    return (
      <div className={cn("page", s.root)}>
        <Loading />
      </div>
    );
  }

  // RENDER:
  return (
    <div
      className={cn("page", s.root)}
      onClick={isFullPage ? () => {} : () => onBoxClick("/trend")}
    >
      <TrendChartPageHeader
        isStacked={isStacked}
        isBoxed={isBoxed}
        onStackedClick={onStackedClick}
      />

      <div
        className={cn(s.chartAndLegend, { [s.chartAndLegendMobile]: mobile })}
      >
        {isStacked ? (
          <AreaChartStacked
            data={trendsChartData}
            categories={[...categoriesFiltered].reverse()}
            factor={factor}
          />
        ) : (
          <AreaChartNonstacked
            data={trendsChartData}
            categories={[...categoriesFiltered].reverse()}
            factor={factor}
          />
        )}

        {isFullPage && (
          <CategorySelector
            categories={categories}
            categoriesFiltered={categoriesFiltered}
            onCategoryClick={onCategoryClick}
          />
        )}
      </div>
    </div>
  );
};

export default TrendChartPage;
