import { /*HashAndCategory,*/ Rule } from "core/types";
import { TransactionClass } from "core/TransactionClass";
import { populateHashAndCategoryObjectArray } from "./categories_js.js";

type OperationType = (s1: string, s2: string, caseSensitive: string) => boolean;

const equals: OperationType = (
  s1: string,
  s2: string,
  caseSensitive: string
): boolean =>
  caseSensitive === "Yes" ? s1 === s2 : s1.toLowerCase() === s2.toLowerCase();

const includes: OperationType = (
  s1: string,
  s2: string,
  caseSensitive: string
): boolean =>
  caseSensitive === "Yes"
    ? s1.includes(s2)
    : s1.toLowerCase().includes(s2.toLowerCase());

const startsWith: OperationType = (
  s1: string,
  s2: string,
  caseSensitive: string
): boolean =>
  caseSensitive === "Yes"
    ? s1.startsWith(s2)
    : s1.toLowerCase().startsWith(s2.toLowerCase());

const ruleMatches = (transaction: TransactionClass, rule: Rule) => {
  const fieldValue = transaction.getFieldValueString(rule.field);
  const operation = rule.operation;
  const caseSensitive = rule.caseSensitive;
  const value1 = rule.value1;

  const op = (name: string, func: OperationType) =>
    name === operation && func(fieldValue, value1, caseSensitive);

  if (op("= v1", equals)) return true;

  if (op("includes v1", includes)) {
    if (rule.lessMoreAmount) {
      const lessMoreAmount = Number(rule.lessMoreAmount);
      const amount = Math.abs(transaction.amount) / 100;
      return (
        (rule.lessMore === "<" && amount < lessMoreAmount) ||
        (rule.lessMore === ">=" && amount >= lessMoreAmount)
      );
    } else {
      return true;
    }
  }

  if (op("starts with v1", startsWith)) return true;

  return false;
};

// TODO: REMOVE THIS LATER, AS getCategoryRule SHOULD BE ENOUGH
export const getCategory = (
  transaction: TransactionClass,
  rules: Rule[]
): string => {
  if (!rules) return "category-not-found-1"; // used? remove?

  const rule = getCategoryRule(transaction, rules);

  return rule ? rule.category : "category-not-found-2";
};

export const getCategoryRule = (
  transaction: TransactionClass,
  rules: Rule[]
): Rule | null => {
  if (!rules) return null;

  return rules.find(rule => ruleMatches(transaction, rule)) || null;
};

export const calculateHashAndCategoryArray = (
  transactions: TransactionClass[],
  rules: Rule[]
): object => {
  const hashAndCategoryObjectArray = {};
  populateHashAndCategoryObjectArray(
    hashAndCategoryObjectArray,
    transactions,
    rules,
    getCategory
  );
  return hashAndCategoryObjectArray;
};
