import React from "react";
import { BarChart, CartesianGrid, XAxis, YAxis, Tooltip, Bar } from "recharts";
import { CategoryAndCent } from "core/types";
import s from "./PercentageBarChart.module.css";

type Props = {
  width: number;
  total: number;
  categoriesAndCents: CategoryAndCent[];
};

// ----------
// COMPONENT:
// Bar chart for mobile
const PercentageBarChart = ({ width, total, categoriesAndCents }: Props) => {
  const barChartData = categoriesAndCents
    .slice(categoriesAndCents.length - 20)
    .map(x => ({
      ...x,
      name: x.name.replace(" ", "-"),
      value: (x.value / total / 100).toFixed(3),
    }))
    .reverse();

  return (
    <div className={s.root}>
      <BarChart
        width={width ? width - 30 : 800}
        height={600}
        layout="vertical"
        data={barChartData}
        maxBarSize={100}
        margin={{ left: 50 }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis type="number" />
        <YAxis dataKey="name" type="category" />
        <Tooltip />
        {/* <Legend /> */}
        <Bar dataKey="value" /* unit="%" */ fill="#8884d8" />
        {/* <Bar dataKey="uv" fill="#82ca9d" /> */}
      </BarChart>
    </div>
  );
};

export default PercentageBarChart;
