import React from "react";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  // Tooltip
} from "recharts";
import { useWindowSize } from "hooks";
import { SELECT_ARRAYS } from "core/constants";
import { getCategoryColour } from "core/colours";
import s from "./AreaChartStacked.module.css";

type Props = {
  data: any[];
  categories: string[];
  factor?: number;
};

const { CATEGORIES } = SELECT_ARRAYS;

// ----------
// COMPONENT:
// https://recharts.org/en-US/examples/StackedAreaChart
const AreaChartStacked = ({ data, categories, factor = 1 }: Props) => {
  // HOOKS:
  const { mobile, width, height } = useWindowSize();

  return (
    <div className={s.root}>
      <AreaChart
        width={mobile ? width - 20 : 1050 * factor}
        height={mobile ? height - 200 : 800 * factor}
        data={data}
        margin={{
          top: 0,
          right: 0,
          left: mobile ? -20 : -10,
          bottom: 0,
        }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="name" />
        <YAxis />
        {/* <Tooltip /> */}
        {categories.map((category: string) => (
          <Area
            baseLine={800 * factor}
            key={category}
            type="monotone"
            dataKey={category}
            stackId="1"
            stroke="#ffffff"
            fill={getCategoryColour(CATEGORIES, category)}
          />
        ))}
      </AreaChart>
    </div>
  );
};

export default AreaChartStacked;
