import React, { useState, useContext } from "react";
import { AppContext, AppContextType } from "appContext";
import { VoidFunction } from "core/types";
import { Icon, IconButton } from "components";
import s from "./Modal.module.css";

const ContentDefault: JSX.Element = <div>dummy</div>;
const OnYesClickDefault: VoidFunction = () => {};

// ----------
// COMPONENT:
const Modal = () => {
  // CONTEXT:
  const context: AppContextType = useContext(AppContext);

  // STATE:
  const [opened, setOpened] = useState(false);
  const [component, setComponent] = useState(ContentDefault);
  const [onYesClick, setOnYesClick] = useState(() => OnYesClickDefault);

  context.openModal = (
    componentInput: JSX.Element,
    onYesClickInput: VoidFunction
  ): void => {
    console.log("*** onYesClickInput:", onYesClickInput);
    setOpened(true);
    setComponent(componentInput);
    setOnYesClick(() => onYesClickInput);
  };

  // FUNCTIONS:
  const onCancelClick = () => {
    setOpened(false);
  };
  const handleYesClick = () => {
    onYesClick();
  };

  if (!opened) {
    return null;
  }

  console.log("*** onYesClick:", onYesClick);

  return (
    <div className={s.root}>
      <div className={s.box}>
        <div className={s.header + " flexRowLeft"}>
          <div>Confirm</div>
        </div>

        <div className={s.component + " flexRowLeft"}>
          <Icon name="question" />
          {component}
        </div>

        <div className={s.footer}>
          <IconButton icon="yes" text="Yes" onClick={handleYesClick} />
          <IconButton icon="no" text="Cancel" onClick={onCancelClick} />
        </div>
      </div>
    </div>
  );
};

export default Modal;
