import React, { useState, useEffect } from "react";
import { useWindowSize } from "hooks";
import { TRANSACTION_COUNT } from "core/constants";
import { centsToDecimal } from "core/utils";
import { Loading, PercentageBarChart, PercentagePieChart } from "components";
import { fetchRules } from "services/rules";
import { fetchTransactions } from "services/transactions";
import { getExpensesByCategoryGraphData } from "process/charts";
import cn from "classnames";
import s from "./PercentageChartPage.module.css";

import testdata from "testdata/expensesByCategoryGraphData.json";

type CategoryAndCent = {
  name: string;
  value: number;
  label: string;
};
const categoriesAndCentsDefault: CategoryAndCent[] = [];

type Props = {
  isBoxed?: boolean;
  cloud: boolean;
  onBoxClick?: (target: string) => void;
};

// -------------------------------------
// COMPONENT:
// https://recharts.org/en-US/api/PieChart
// https://recharts.org/en-US/examples/PieChartWithCustomizedLabel
const PercentageChartPage = ({
  isBoxed = false,
  cloud,
  onBoxClick = () => {},
}: Props) => {
  // HOOKS:
  const { desktop, mobile, width } = useWindowSize();

  const isFullPage = !isBoxed;
  const title = "Percentage: expenses per category (€/mo)";

  const getFactors = (): number[] => {
    if (isBoxed) return [0.61, 2, 1.75, 1.7];
    if (mobile) return [0.42, 0.5, 1.75, 1.7];
    return [1, 1, 1, 1];
  };

  const [factor, outerRadiusFactor, heightFactor, cyFactor] = getFactors();

  // STATE:
  const [categoriesAndCents, setCategoriesAndCents] = useState(
    categoriesAndCentsDefault
  );
  const [total, setTotal] = useState(0);
  const [from, setFrom] = useState("");
  const [to, setTo] = useState("");
  const [dayCount, setDayCount] = useState(0);
  const [totalPerMonth, setTotalPerMonth] = useState(0);

  const getData = async () => {
    if (cloud) {
      return testdata;
    } else {
      const r = await fetchRules(cloud);
      const t = await fetchTransactions(0, TRANSACTION_COUNT, cloud);
      const data = getExpensesByCategoryGraphData(t, r);
      return data;
    }
  };

  // EFFECTS - GET DATA:
  useEffect(() => {
    const fetchData = async () => {
      const {
        graphDataItems,
        total,
        from,
        to,
        dayCount,
        totalPerMonth,
      } = await getData();

      setCategoriesAndCents(graphDataItems);
      setTotal(total);
      setFrom(from);
      setTo(to);
      setDayCount(dayCount);
      setTotalPerMonth(totalPerMonth);
    };
    fetchData();
    return () => {}; // on unmount
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [cloud]);

  if (isFullPage && (categoriesAndCents.length === 0 || !from || !to)) {
    return <Loading />;
  }

  return (
    <div
      className={cn("page", s.root)}
      onClick={isFullPage ? () => {} : () => onBoxClick("/percentage")}
    >
      {isFullPage ? <h3>{title}</h3> : <h4>{title}</h4>}

      {desktop && (
        <PercentagePieChart
          factor={factor}
          heightFactor={heightFactor}
          cyFactor={cyFactor}
          outerRadiusFactor={outerRadiusFactor}
          categoriesAndCents={categoriesAndCents}
        />
      )}

      {mobile && (
        <PercentageBarChart
          width={width}
          total={total}
          categoriesAndCents={categoriesAndCents}
        />
      )}

      {isFullPage && (
        <>
          <div>
            {from} - {to}
          </div>
          <div>€{totalPerMonth} a month</div>
          <div>
            €{total} in {dayCount} days
          </div>

          {desktop && (
            <>
              <br />
              <table className={s.categoryTable}>
                <thead>
                  <tr>
                    <th>Category</th>
                    <th>Month</th>
                    <th>%</th>
                    <th>Period</th>
                  </tr>
                </thead>
                <tbody>
                  {[...categoriesAndCents].reverse().map((item, index) => (
                    <tr key={`${item.name}_${index}`}>
                      <td>{item.name}</td>

                      {item.label ? (
                        item.label
                          .replace(item.name, "")
                          .replace("(", "")
                          .replace(")", "")
                          .replace("%", "")
                          .split(" ")
                          .filter(Boolean) // delete empty
                          .map((x, index) => <td key={index}>{x}</td>)
                      ) : (
                        <>
                          <td key="a"></td>
                          <td key="b"></td>
                        </>
                      )}

                      <td>{centsToDecimal(item.value.toString())}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </>
          )}
        </>
      )}
    </div>
  );
};

export default PercentageChartPage;
