import React, { useState } from "react";
import { Route, Switch } from "react-router-dom";
import { AppContext, DefaultContext } from "appContext.ts";
import {
  CategoriesPage,
  HomePage,
  PercentageChartPage,
  RulesPage,
  TransactionsPage,
  TrendChartPage,
} from "./pages";
import { PageHeader, PageFooter } from "components";

const route = (path, page) => <Route path={path}>{page}</Route>;

function App() {
  // https://create-react-app.dev/docs/adding-custom-environment-variables/
  const production = process.env.NODE_ENV === "production";
  const development = process.env.NODE_ENV === "development";

  // STATE:
  const [isCloud, setIsCloud] = useState(production ? true : false);
  const [isMasked, setIsMasked] = useState(production ? true : false);

  // CLICK:
  const onCloudClick = () => setIsCloud(production ? true : !isCloud);
  const onMaskClick = () => setIsMasked(production ? true : !isMasked);

  return (
    <AppContext.Provider value={DefaultContext}>
      <PageHeader
        cloud={isCloud}
        onCloudClick={onCloudClick}
        mask={isMasked}
        onMaskClick={onMaskClick}
      />

      <Switch>
        <Route
          path="/transactions/transaction=:id"
          render={({ match, isMasked }) => (
            <TransactionsPage match={match} isMasked={isMasked} />
          )}
        />

        {route(
          "/transactions",
          <TransactionsPage cloud={isCloud} isMasked={isMasked} />
        )}

        <Route
          path="/rules/transaction=:id"
          render={({ match }) => <RulesPage match={match} mask={isMasked} />}
        />

        {route("/percentage", <PercentageChartPage cloud={isCloud} />)}
        {route("/trend", <TrendChartPage cloud={isCloud} />)}

        {development && route("/rules", <RulesPage mask={isMasked} />)}
        {development && route("/categories", <CategoriesPage />)}
        {route("/", <HomePage />)}
      </Switch>

      <PageFooter />
    </AppContext.Provider>
  );
}

export default App;
