import { Json, Response } from "core/types";

export const fetchUrl = async (url: string) => {
  const response = await fetch(url);
  return await response.json();
};

export const deleteUrl = async (url: string) => {
  fetch(url, {
    method: "DELETE",
    // headers: { "content-type": "application/json" },
    // body: JSON.stringify({ id: 1 })
  })
    .then(res => res.text()) // OR res.json()
    .then(res => console.log(res));
};

function handleErrors(response: Response) {
  if (!response.ok) {
    throw Error(response.statusText);
  }
  return response;
}

export const postJson = (json: Json, url: string) => {
  // const response =
  fetch(url, {
    method: "POST", // *GET, POST, PUT, DELETE, etc.
    mode: "cors", // no-cors, *cors, same-origin
    cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
    credentials: "same-origin", // include, *same-origin, omit
    headers: {
      "Content-Type": "application/json",
      // "Content-Type": "application/x-www-form-urlencoded"
    },
    redirect: "follow", // manual, *follow, error
    referrer: "no-referrer", // no-referrer, *client
    // body: JSON.stringify(`{${rule}}`) // body data type must match "Content-Type" header
    body: JSON.stringify(json), // body data type must match "Content-Type" header
  })
    .then(handleErrors)
    .then(response => console.log("POST OK", response))
    .catch(error => console.log("POST ERROR:", error));

  // return response.json(); // parses JSON response into native JavaScript objects
};

