import React from "react";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
} from "recharts";
import { useWindowSize } from "hooks";
import { SELECT_ARRAYS } from "core/constants";
import { getCategoryColour } from "core/colours";
import s from "./AreaChartNonstacked.module.css";

type Props = {
  data: any[];
  categories: string[];
  factor?: number;
};

const { CATEGORIES } = SELECT_ARRAYS;

// ----------
// COMPONENT:
// https://recharts.org/en-US/api/AreaChart
const AreaChartNonstacked = ({ data, categories, factor = 1 }: Props) => {
  // HOOKS:
  const { mobile, width, height } = useWindowSize();

  return (
    <div className={s.root}>
      <AreaChart
        width={mobile ? width - 20 : 1050 * factor}
        height={mobile ? height - 200 : 800 * factor}
        data={data}
        margin={{
          top: 0,
          right: 0,
          left: mobile ? -20 : -10,
          bottom: 0,
        }}
      >
        <defs>
          {categories.map((category: string, index) => {
            const colour = getCategoryColour(CATEGORIES, category);
            return (
              <linearGradient
                key={category}
                id={`color${index}`}
                x1="0"
                y1="0"
                x2="0"
                y2="1"
              >
                <stop offset="5%" stopColor={colour} stopOpacity={0.9} />
                <stop offset="95%" stopColor={colour} stopOpacity={0.1} />
              </linearGradient>
            );
          })}
        </defs>
        <XAxis dataKey="name" />
        <YAxis />
        <CartesianGrid strokeDasharray="3 3" />
        <Tooltip />
        {categories.map((category: string, index) => (
          <Area
            baseLine={800 * factor}
            key={category}
            type="monotone"
            dataKey={category}
            stroke={getCategoryColour(CATEGORIES, category)}
            fillOpacity={0.7}
            fill={`url(#color${index})`}
          />
        ))}
      </AreaChart>
    </div>
  );
};

export default AreaChartNonstacked;
