import React from "react";
import cn from "classnames";
import s from "./Icon.module.css";

type Props = {
  name: string;
  small?: boolean;
  onClick?: () => void;
};

const Icon = ({ name, small, onClick = () => {} }: Props) => {
  return (
    <div
      className={cn(s.root, s[name], {
        [s.small]: small,
        [s.clickable]: onClick,
      })}
      onClick={onClick}
    />
  );
};

export default Icon;
