import React from "react";
import cn from "classnames";
import { TransactionClass } from "core/TransactionClass";
import s from "./FieldCopier.module.css";

type Props = {
  transaction: TransactionClass;
  onClick: (fieldValue: string, fieldName: string) => void;
};

// ----------
// COMPONENT:
const FieldCopier = ({ transaction, onClick }: Props) => {
  const copyButton = (fieldName: string, fieldValue: string) => (
    <button onClick={() => onClick(fieldValue, fieldName)}>{fieldName}</button>
  );

  return (
    <div className={cn(s.root, "flexRowWithMargin")}>
      <div>Copy:</div>
      {copyButton("Name", transaction.name)}
      {copyButton("Counter account", transaction.counterAccount)}
      {copyButton("Type", transaction.type)}
      {copyButton("Remarks", transaction.remarks)}
      {copyButton("Hash", transaction.hash.toString())}
    </div>
  );
};

export default FieldCopier;
