import React from "react";
import { getCategoryIcon, CATEGORIES } from "core/constants";
import { getCategoryColour } from "core/colours";
import { CategoryIcon } from "components";
import cn from "classnames";
import s from "./CategoryIconAndLabel.module.css";

type Props = {
  item: string;
  fullWidth: boolean;
  index?: number;
  iconOnly?: boolean;
  onChange?: (event: any) => void;
};

// ----------
// COMPONENT:
const CategoryIconAndLabel = ({
  item,
  fullWidth,
  index,
  iconOnly = false,
  onChange,
}: Props) => {
  const onClick = onChange ? () => onChange(index) : () => {};

  if (
    item === "category-not-found-1" ||
    item === "category-not-found-2" ||
    item === "category-not-found-3"
  ) {
    return null;
  }

  return (
    <div
      className={cn(s.root, { [s.fullWidth]: fullWidth })}
      style={{ backgroundColor: getCategoryColour(CATEGORIES, item) }}
      onClick={onClick}
    >
      <span className={s.itemIcon}>
        <CategoryIcon category={getCategoryIcon(item)} />
      </span>
      {!iconOnly && item}
    </div>
  );
};

export default CategoryIconAndLabel;
