import React from "react";
import { PieChart, Pie, Cell /* PieProps */ } from "recharts";
import { SELECT_ARRAYS } from "core/constants";
import { CategoryAndCent } from "core/types";
import { getCategoryColour } from "core/colours";
import s from "./PercentagePieChart.module.css";

const { CATEGORIES } = SELECT_ARRAYS;

type Props = {
  factor: number;
  heightFactor: number;
  cyFactor: number;
  outerRadiusFactor: number;
  categoriesAndCents: CategoryAndCent[];
};

// ----------
// COMPONENT:
// Pie chart for desktop
const PercentagePieChart = ({
  factor,
  heightFactor,
  cyFactor,
  outerRadiusFactor,
  categoriesAndCents,
}: Props) => {
  // FUNCTIONS:
  type PieProps2 = { index: number };
  const renderCustomizedLabel2 = (props: any /* PieProps */): SVGElement => {
    const { index } = (props as unknown) as PieProps2;
    return (categoriesAndCents[index].label as unknown) as SVGElement;
  };

  return (
    <div className={s.root}>
      <PieChart width={800 * factor} height={450 * factor * heightFactor}>
        <Pie
          data={categoriesAndCents}
          cx={400 * factor}
          cy={225 * factor * cyFactor}
          labelLine={false}
          label={renderCustomizedLabel2}
          // label={isFullPage ? renderCustomizedLabel : () => ""}
          outerRadius={180 * factor * outerRadiusFactor}
          fill="#8884d8"
          dataKey="value"
          isAnimationActive={false}
          // blendStroke={true}
        >
          {categoriesAndCents.map(item => (
            <Cell
              key={item.name}
              fill={getCategoryColour(CATEGORIES, item.name)}
            />
          ))}
        </Pie>
      </PieChart>
    </div>
  );
};

export default PercentagePieChart;
