import React, { useState } from "react";
import { useModal } from "hooks";
import { TransactionClass } from "core/TransactionClass";
import { SELECT_ARRAYS, operationIncludesValue2 } from "core/constants";
import { addRule } from "services/rules";
import { NewRuleHeader, NewRuleTable } from "components";
import s from "./NewRule.module.css";

type Props = {
  transaction: TransactionClass;
  onSaveClick: () => void;
};

// ----------
// COMPONENT:
const NewRule = ({ transaction, onSaveClick: propsOnSaveClick }: Props) => {
  // STATE:
  const [categoryIndex, setCategoryIndex] = useState(0);
  const [fieldIndex, setFieldIndex] = useState(0);
  const [operationIndex, setOperationIndex] = useState(0);
  const [caseSensitivityIndex, setCaseSensitivityIndex] = useState(0);
  const [value1, setValue1] = useState("");
  const [value2, setValue2] = useState("");
  const [lessMoreIndex, setLessMoreIndex] = useState(0);
  const [lessMoreAmount, setLessMoreAmount] = useState("0.00");

  // CUSTOM HOOKS:
  const { openModal } = useModal();

  const { CATEGORIES, FIELDS, NO_YES, OPERATIONS, LESS_MORE } = SELECT_ARRAYS;

  // SELECT & VALUE CHANGES:
  const onCategoryChange = (index: number) => {
    setCategoryIndex(index);
  };
  const onFieldChange = (event: any) => {
    setFieldIndex(event.target.value);
  };
  const onOperationChange = (event: any) => {
    setOperationIndex(event.target.value);
  };
  const onCaseSensitivityChange = (event: any) => {
    setCaseSensitivityIndex(event.target.value);
  };
  const onValueChange = (event: any, valueIndex: number) => {
    switch (valueIndex) {
      case 1:
        setValue1(event.target.value);
        break;
      case 2:
        setValue2(event.target.value);
        break;
      default:
        break;
    }
  };
  const onLessMoreChange = (event: any) => {
    setLessMoreIndex(event.target.value);
  };
  const onLessMoreAmountChange = (value: string) => {
    setLessMoreAmount(value);
  };

  // BUTTON CLICKS:
  const onCopyClick = (fieldValue: string, fieldName: string) => {
    const fieldIndex = FIELDS.indexOf(fieldName);
    setFieldIndex(fieldIndex);
    setValue1(fieldValue);
  };
  const onSaveClick = () => {
    openModal(
      <div>Are you sure you want to save this rule?</div>,
      onSaveConfirm
    );
  };
  const onSaveConfirm = () => {
    addRule({
      id: 0,
      category: CATEGORIES[categoryIndex],
      field: FIELDS[fieldIndex],
      operation: OPERATIONS[operationIndex],
      caseSensitive: NO_YES[caseSensitivityIndex],
      value1: value1,
      value2: operationIncludesValue2(operationIndex) ? value2 : null,
      lessMore: lessMoreAmount === "0.00" ? null : LESS_MORE[lessMoreIndex],
      lessMoreAmount: lessMoreAmount === "0.00" ? null : lessMoreAmount,
    });

    propsOnSaveClick();
  };

  return (
    <div className={s.root}>
      <NewRuleHeader
        transaction={transaction}
        onCopyClick={onCopyClick}
        onSaveClick={onSaveClick}
      />

      <NewRuleTable
        categoryIndex={categoryIndex}
        fieldIndex={fieldIndex}
        operationIndex={operationIndex}
        caseSensitivityIndex={caseSensitivityIndex}
        value1={value1}
        value2={value2}
        lessMoreIndex={lessMoreIndex}
        lessMoreAmount={lessMoreAmount}
        onCategoryChange={onCategoryChange}
        onFieldChange={onFieldChange}
        onOperationChange={onOperationChange}
        onCaseSensitivityChange={onCaseSensitivityChange}
        onValueChange={onValueChange}
        onLessMoreChange={onLessMoreChange}
        onLessMoreAmountChange={onLessMoreAmountChange}
      />
    </div>
  );
};

export default NewRule;
