import React from "react";
import { useWindowSize } from "hooks";
import s from "./TransactionsTableHeader.module.css";

type Props = {
  isHashVisible: boolean;
};

// ----------
// COMPONENT:
const TransactionsTableHeader = ({ isHashVisible }: Props) => {
  // HOOKS:
  const { desktop } = useWindowSize();

  return (
    <thead className={s.root}>
      <tr>
        {desktop && <th>#</th>}
        <th>Date</th>
        <th>Name</th>
        {desktop && <th>Counter account</th>}
        {desktop && <th>Code</th>}
        <th>{desktop ? "Amount" : "€"}</th>
        {desktop && <th>Type</th>}
        {desktop && <th>Remarks</th>}
        {isHashVisible && <th>Hash</th>}
        <th>{desktop ? "Category" : "Cat"}</th>
      </tr>
    </thead>
  );
};

export default TransactionsTableHeader;
