import { CODE } from "core/constants";
import {
  centsToDecimal,
  convert20191231to31_Dec_2019,
  getObjectValueString,
} from "core/utils";

const fieldsToRandomise = ["name", "remarks", "counterAccount"];
const ascii_A = 65;
const ascii_Z = 90;
const ascii_a = 97;
const ascii_z = 122;
const ascii_0 = 48;
const ascii_9 = 57;

const randomiseCharCode = (charCode: number): string => {
  let next = charCode;

  if (ascii_A <= charCode && charCode <= ascii_Z) {
    next = charCode + Math.floor(Math.random() * 26);
    if (next > ascii_Z) next -= 26;
  }

  if (ascii_a <= charCode && charCode <= ascii_z) {
    next = charCode + Math.floor(Math.random() * 26);
    if (next > ascii_z) next -= 26;
  }

  if (ascii_0 <= charCode && charCode <= ascii_9) {
    next = charCode + Math.floor(Math.random() * 10);
    if (next > ascii_9) next -= 10;
  }

  return String.fromCharCode(next);
};

const randomiseString = (value: string): string => {
  const randomised = [];
  for (var i = 0; i < value.length; i++) {
    randomised.push(randomiseCharCode(value.charCodeAt(i)));
  }
  return randomised.join("");
};

const removeLast4Chars = (s: string) => s.substring(0, s.length - 4);

export const processField = (
  value: string,
  name: string,
  isMasked?: boolean
): string => {
  if (name === "dateShort")
    return removeLast4Chars(convert20191231to31_Dec_2019(value));
  if (name === "date") return convert20191231to31_Dec_2019(value);

  if (name === "code") return getObjectValueString(CODE, value);

  if (name === "amount") {
    return isMasked
      ? randomiseString(centsToDecimal(value))
      : centsToDecimal(value);
  }

  return isMasked && fieldsToRandomise.includes(name)
    ? randomiseString(value)
    : value;
};
