import React, { useState, useEffect } from "react";
import { useNotifications } from "hooks";
import { Match } from "core/types";
import { TransactionClass } from "core/TransactionClass";
import { getIdFromMatch } from "core/utils";
import { Loading, TransactionList, TransactionsPageHeader } from "components";
import { fetchRules } from "services/rules";
import { fetchTransactions } from "services/transactions";
import { extendTransactions } from "process/transactions";
import cn from "classnames";
import s from "./TransactionsPage.module.css";

const PAGE_SIZE = 10;

const transactionsDefault: TransactionClass[] = [];

type Props = {
  match: Match;
  cloud: boolean;
  isMasked: boolean;
};

// ----------
// COMPONENT:
const TransactionsPage = ({ match, cloud, isMasked }: Props) => {
  const development = process.env.NODE_ENV === "development";
  const id: number = getIdFromMatch(match) || 1;
  const pageIndexDefault = Math.floor((id - 1) / PAGE_SIZE);

  // STATE:
  // pageIndex: value 0-indexed, display 1-indexed
  const [pageIndex, setPageIndex] = useState(pageIndexDefault);
  const [transactions, setTransactions] = useState(transactionsDefault);

  // CUSTOM HOOKS:
  const { info } = useNotifications();

  // EFFECTS - GET DATA:
  useEffect(() => {
    const fetchData = async () => {
      const r = await fetchRules(cloud);
      const start = pageIndex * PAGE_SIZE;
      const end = start + PAGE_SIZE;
      const fetched = await fetchTransactions(start, end, cloud);
      if (fetched) {
        info(`Transactions fetched: ${start + 1}..${end}`);
        const extended = extendTransactions(fetched, start, r);
        setTransactions(extended);
      } else {
        console.error("ERROR: fetchTransactions empty");
      }
    };
    fetchData();
    return () => {}; // on unmount
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pageIndex, cloud]);

  // CLICKS:
  const onTransactionsPageHeaderButtonClick = (increment: number): void => {
    if (increment === 0) {
      setPageIndex(0);
      return;
    }

    const newIndex = pageIndex + increment;
    if (newIndex < 0) return;

    setPageIndex(newIndex);
  };

  if (!transactions || transactions.length === 0) {
    return <Loading />;
  }

  return (
    <div className={cn("page", s.root)}>
      <TransactionsPageHeader
        pageIndex={pageIndex}
        onClick={onTransactionsPageHeaderButtonClick}
      />

      {transactions && transactions.length > 0 && (
        <TransactionList
          transactions={transactions}
          clickable={development ? true : false}
          urlId={id}
          isHashVisible={false}
          isMasked={isMasked}
        />
      )}
    </div>
  );
};

export default TransactionsPage;
